.messages_list_wrapper {
    height: calc(100% - 50px);
    overflow-y: scroll;

    flex-shrink: 0;
    flex-grow: 0;
}

.messages_list_wrapper::-webkit-scrollbar {
    width: 0;  /* Remove scrollbar space */
    background: transparent;  /* Optional: just make scrollbar invisible */
}
/* Optional: show position indicator in red */
.messages_list_wrapper::-webkit-scrollbar-thumb {
    background: transparent;
}

.messages_list {
    color: whitesmoke;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    margin: 0;
    min-height: 100%;
}

.message_wrapper {
    margin-right: 20px;
    list-style: none;
    max-width: 90%;
    flex-shrink: 0;
    display: flex;
}

.messages_list .message {
    background-color: rgba(113, 121, 147, 0.4);
    padding: 10px;
    padding-bottom: 15px;
    padding-right: 20px;
    border-radius: 7px;
    display: inline-block;
    position: relative;
    max-width: calc(100% - 30px);
}

.message__text {
    display: inline-block;
    max-width: 100%;
    word-wrap: break-word;
}

.messages_list .message_wrapper:last-child {
    margin-bottom: 10px;
}

.message_wrapper.my {
    align-self: flex-end;
    margin-right: 0;
    margin-left: 20px;
    flex-direction: row-reverse;
}

.messages_list .message.my {
    background-color: rgba(41, 161, 248, 0.4);
}

.messages_list li {
    flex-basis: 20px;
}

.messages_list .message_wrapper + .message_wrapper {
    margin-top: 10px;
}

.message__time {
    font-size: 9px;
    position: absolute;
    right: 5px;
    bottom: 2px;
    color: rgba(192, 198, 217, 0.4)
}

.message--system {
    list-style: none;
    align-self: flex-end;
    padding: 20px;
    color: #b6b2b2;
    font-style: italic;
    box-sizing: border-box;
    text-align: right;
}

.message--system--error {
    color: #ff6e6e;
}