.message_wrapper.my .user__avatar,
.user_list__user.my .user__avatar {
    background-color: rgba(41, 161, 248, 0.3);;
}

.message_wrapper.my .user__avatar {
    margin-left: 5px;
    margin-right: 0;
}
.message_wrapper .user__avatar {
    margin-right: 5px;
}

.user__avatar {
    border-radius: 5px;
    width: 45px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    flex-shrink: 0;

    background-color: rgba(187, 187, 187, 0.3);
}
.user__avatar span {
    line-height: 100%;
}

.user__avatar.micro {
    border-radius: 3px;
    width: 25px;
    height: 25px;
    font-size: 18px;
}

.message_wrapper.continues .user__avatar {
    visibility: hidden;
}