
.main_sidebar__user_list {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
}

.user_list__user {
    list-style: none;
    display: flex;
    color: whitesmoke;
    align-items: center;
    background-color: rgba(182, 178, 178, 0);
    border-radius: 5px;
    transition: background-color .3s;
    cursor: pointer;
}

.user_list__user + .user_list__user {
    margin-top: 10px;
}

.user_list__user:hover {
    background-color: rgba(182, 178, 178, 0.1);
}

.user__name {
    margin-left: 10px;
}

.main_sidebar__user_list.min .user__name {
    display: none;
}

.main_sidebar__user_list.min {
    flex-direction: row;
}

.main_sidebar__user_list.micro {
    flex-direction: column;
    align-items: center;
}

.main_sidebar__user_list.min .user_list__user + .user_list__user {
    margin-top: unset;
    margin-left: 10px;
}