.message_editor_container {
    display: flex;
    width: 100%;
    height: 50px;

    background-color: rgba(85, 89, 102, 0.4);
    border-radius: 10px;
}

#msg_editor_textarea {
    flex-grow: 1;
    height: 100%;
    border: none;
    resize: none;
    outline: none;
    color: whitesmoke;
    box-sizing: border-box;
    padding: 10px;
    background: transparent;

    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

/* Hide scrollbar for Chrome, Safari and Opera */
#msg_editor_textarea::-webkit-scrollbar {
    display: none;
}

.message_editor__send_button {
    width: 40px;
    border-radius: 10px;
    border: none;
    outline: none;
    background-image: url("../../assets/send.png");
    background-position: center;
    background-size: 50%;
    background-repeat: no-repeat;
    background-color: unset;
    cursor: pointer;
    margin: 0 10px;
}

