.main_sidebar {
    width: 30vw;
    padding-top: 20px;
    min-width: 150px;
}

.main_sidebar__container {
    padding: 0 20px;
}

.main_sidebar__title {
    color: #b6b2b2;
    font-weight: normal;
    margin: 0;
    margin-bottom: 10px;
}

.main_sidebar.micro {
    width: 50px;
    min-width: 50px;
}

.main_sidebar.micro .main_sidebar__title {
    display: none;
}

.main_sidebar.micro .main_sidebar__container {
    padding: 0;
}

