.window {
    background-color: rgba(16, 18, 27, .3);
    display: flex;
    flex-direction: column;
    border-radius: 14px;
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
}

.window__container {
    width: 100%;
    padding: 5px 10px;
}

.window__header {
    width: 100%;
    min-height: 60px;
    border-bottom: 1px solid rgba(196, 196, 196, 0.2);
}

.window_header__container {
    padding: 0 20px;
    display: flex;

    align-items: center;
    height: 100%;
}

.window__controls {
    width: 10px;
    height: 10px;
    background-color: #f96057;
    border-radius: 50%;
    box-shadow: 20px 0 0 0 #f8ce52, 40px 0 0 0 #5fcf65;
    margin-right: 195px;
    flex-shrink: 0;
}

.window__inner {
    flex-grow: 1;
    display: flex;
    max-height: calc(100% - 60px);
}

.window__sidebar {
    overflow: hidden;
    flex-shrink: 0;
}

.window__content {
    background-color: rgba(16, 18, 27, .4);
    flex-grow: 1;
    height: 100%;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    list-style: none;
    max-width: calc(100% - 30vw);
}

@media (max-width: 500px) {
    .window__content {
        max-width: calc(100% - 50px);
    }
}

